import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Typography } from "@rp/react/web/mui/core"
import { Search } from "@rp/react/web/mui/icons"

type Props = { onClick?: () => void }

const MobileQueryBarSearchText = ({ onClick }: Props) => {
  const { isMobile } = useDeviceInfo()
  if (!isMobile) return null

  return (
    <Typography
      onClick={onClick}
      sx={theme => ({
        pl: 1,
        pr: 1.5,
        whiteSpace: "nowrap",
        fontSize: 12,
        color: theme.palette.text.disabled,
        display: "flex",
        alignItems: "center",
        gap: 0.5,
        svg: {
          fontSize: 16,
        },
      })}
    >
      <Search /> Add to Search
    </Typography>
  )
}

export { MobileQueryBarSearchText }
